.navbar {
  width: 100%;
  height: 70px;
  background-color: #09357b;
  color: #ffffff;
  text-align: -webkit-center;

  position: fixed;
  left: 0;
  top: 0;
  margin-top: 0%;
  z-index: 999;
}

.wrap {
  max-width: 1100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 25px;
  padding-left: 25px;
}

.menu {
  display: flex;
  align-items: center;
}

.menu li {
  padding: 1rem;
  color: #ffffff;
}

.navbar a {
  font-size: 0.8rem;
}

.smallScreenMenu {
  display: none;
}

.close {
  color: #09357b;
}

@media screen and (max-width: 720px) {
  .smallScreenMenu {
    display: block;
    position: absolute;
    right: 1rem;
    cursor: pointer;
    z-index: 10;

    padding-bottom: 25px;
  }
  .wrap {
    padding-top: 16px;
  }
  .menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    left: -100%;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: #f8f8f8;
    z-index: 10;
    transition: left 1s;
  }

  .active {
    left: 0;
  }

  .navbar a {
    font-size: 2rem;
    color: black;
  }

  /* .logoWrapper {
    width: 50px;
  } */
}
