@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800;900&display=swap");

:root {
  --success-color: #c8e6c9;
  --success-border-color: #4caf50;
  --success-text-color: #388e3c;

  --error-color: #ffcdd2;
  --error-border-color: #f44336;
  --error-text-color: #d32f2f;

  --warning-color: #fff9c4;
  --warning-border-color: #ffeb3b;
  --warning-text-color: #fbc02d;

  --info-color: #bbdefb;
  --info-border-color: #2196f3;
  --info-text-color: #1976d2;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  font-family: "Inter", sans-serif;
}

.container {
  max-width: 1100px;
  overflow: auto;
  padding: 30px 30px;
}

.mtAftrNav {
  margin: 80px auto auto auto;
}

.mNewSearchTitle {
  margin: 0 0 3% 3.5%;
}

.wrapperNewsList {
  display: flex;
  flex-direction: column;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.5rem;
}

.newsGrid :nth-child(5n + 1) {
  grid-column: span 2;
  grid-row: span 2;
}

.newsGrid :not(:first-child) > .nGridItem {
  height: 150px;
}

.newsGrid :not(:first-child) > .itemImg {
  height: 120px;
}

.topNewsCard {
  height: 190px;
  max-height: 200px;
  font-size: 11px;
  font-weight: normal;
}

.topNewsCard > div > div > img {
  height: 100px;
}

.grid-3,
.tNewsSectionTwo-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}

.firstImgCrd {
  max-height: 300px;
  max-width: 506px;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.icnPosition {
  top: 1%;
  right: 1%;
}

.vAlignBtm {
  vertical-align: text-bottom;
}
.fontBold {
  font-weight: bold;
}

.randomImgWrap {
  width: 320px;
  height: 220px;
}
.randomImgWrap > img {
  width: 100%;
  height: 100%;
}
.bookMarkIcn {
  vertical-align: text-bottom;
  font-weight: bold;
  margin-right: 4px;
}

.enableBtn {
  background-color: #09357b;
  color: #ffffff;
}
.disabledBtn {
  background-color: #ccc;
  cursor: not-allowed;
}

.pointer {
  cursor: pointer;
}

.disabledCursor {
  cursor: not-allowed;
}

.mAuto {
  margin: auto auto auto auto;
}

.minH7 {
  min-height: 700px;
}

.mbHalf {
  margin-bottom: 1.5%;
}

.ml40p {
  margin-left: 40px;
}

.searchContainer {
  display: flex;
  justify-content: space-between;
  gap: 2px;
}

.searchBox {
  padding-left: 10px;
  padding-right: 10px;
  color: #ffffff;
}

.searchBoxPosition {
  left: -245px;
  top: 20px;
}

.vAlignCenter {
  vertical-align: middle;
}

.search {
  border: 1px solid #ccc;
  padding: 5px;
  width: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
  float: right;

  width: 200px;
  background-color: #09357b;
  border: none;
  outline: none;
}

.searchFocus {
  background-color: #2153a3;
  border-bottom: 1px solid#ffffff;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.searchFocus::placeholder {
  color: #ffffff;
  opacity: 1;
}

.searchBtn {
  border: none;
  outline: none;
  display: contents;
  color: #ffffff;
}

.searchIcon {
  right: -30px;
}

.fixed {
  position: fixed;
}

.alertWrapper {
  width: 998px;
  padding: 0 0 20px 42px;

  border-radius: 6px;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0.2);
}

.newsCard-border-red {
  border-bottom: 5px solid var(--error-text-color);
}

.newsCard-border-yellow {
  border-bottom: 5px solid var(--warning-text-color);
}

.newsCard-border-blue {
  border-bottom: 5px solid var(--info-text-color);
}

.newsCard-border-green {
  border-bottom: 5px solid var(--success-text-color);
}
.newsSearchHeader {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

@media screen and (max-width: 720px) {
  .newsSearchHeader {
    flex-direction: column;
  }
  .container {
    max-width: 700px;
  }
  .grid-3 {
    grid-template-columns: repeat(2, 1fr);
  }
  .grid-4,
  .tNewsSectionTwo-3 {
    grid-template-columns: repeat(1, 1fr);
    width: 500px;
  }

  .newsGrid :nth-child(5n + 1) {
    grid-column: 1;
    grid-row: 1;
  }

  .nGridItemm,
  .topNewsCard {
    max-height: 300px;
  }

  .topNewsCard {
    height: 290px;
  }

  .topNewsCard > .newsCard {
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .othImgCrd {
    height: 290px;
    max-width: 506px;
  }

  .topNewsCard > div > div > img,
  .othImgCrd > img {
    width: 100%;
    height: 290px;
    object-fit: cover;
  }

  .tNewsSectionTwo {
    margin-top: 10px;
  }

  .search {
    background-color: #ffffff;
    border: 1px solid #09357b;
  }

  .searchFocus::placeholder,
  .searchBox,
  .searchBtn {
    color: #09357b;
  }

  .navSearch {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }

  .searchBoxPosition {
    left: -80px;
    top: -150px;
  }
}

@media screen and (max-width: 630px) {
  .container {
    max-width: 600px;
  }
  .grid-3 {
    grid-template-columns: repeat(1, 1fr);
  }
  .grid-4 {
    grid-template-columns: repeat(1, 1fr);
  }

  .newsGrid :nth-child(5n + 1),
  .nGridItem,
  .topNewsCard,
  .othImgCrd {
    max-width: 300px;
  }

  .grid-4,
  .tNewsSectionTwo-3 {
    width: 300px;
  }

  .tNewsSectionTwo-3 > div,
  .grid-3 > div {
    max-width: 300px;
  }

  .topNewsCard > .newsCard {
    width: 290px;
  }

  .topNewsCard > div > div > img,
  .othImgCrd > img {
    width: 100%;
  }
}
