.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #09357b;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;

  left: 50%;
  position: absolute;
  text-align: center;
  /* top: 50%; */
  top: calc(50% - 70px);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
