.container {
  max-width: 1100px;
  margin: 80px auto;
  overflow: auto;
  padding: 30px 30px;
}

.wrapperNewsList {
  display: flex;
  flex-direction: column;
}

.gap20 {
  gap: 20px;
}

.bgLightGray {
  background-color: #eaeaea;
}

.text11Px {
  font-size: 11px;
}

.trailText {
  color: #909090;
}

.contentWrapper {
  max-width: 970px;
  display: flex;
  justify-content: space-between;
}

.imgContainer {
  width: 320px;
  height: 270px;
}

.articleImg {
  width: 100%;
  object-fit: cover;
}

.conentWidth {
  max-width: 550px;
}

.conentWidth > img {
  max-width: 400px;
  height: 400px;
}

@media screen and (max-width: 720px) {
  .contentWrapper {
    flex-direction: column-reverse;
  }
}
