.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
}

.wrapperNewsList {
  display: flex;
  flex-direction: column;
}

.newsGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.5rem;
}

/* .newsGrid :nth-child(5n + 1) {
  grid-column: span 2;
  grid-row: span 2;
} */

.randomNewsGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}

